label {
    display:inline-block;
}

.btnSubmit {
    padding: 10px 24px;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
}

.btnSubmit:hover {
    padding: 10px 24px;
    background-color: #4CAF50;
    color: white;
}

.btnReset {
    padding: 10px 24px;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
}

.btnReset:hover {
    padding: 10px 24px;
	background-color: #ff0000;
	color: white;
}



